import { createRouter, createWebHistory, RouteRecordRaw } from "vue-router";
import HomeView from "../views/HomeView.vue";

const routes: Array<RouteRecordRaw> = [
  {
    path: "/",
    name: "home",
    component: HomeView,
  },
  {
    path: "/playground",
    name: "Monaco-Editor",
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/PlaygroundView.vue"),
  },
  {
    path: "/code_visualization",
    name: "visualEditor",
    component: () => import("../views/codeVisualizationView.vue"),
  },
  {
    path: "/debug_view",
    name: "debugView",
    component: () => import("../views/debugView.vue"),
  },
  {
    path: "/courselearning/display_course",
    name: "DisplayCourse",
    component: () => import("../views/CourseLearning/DisplayCourse.vue"),
  },
  {
    path: "/courselearning/learning_course",
    name: "LearningCourse",
    component: () => import("../views/CourseLearning/LearningCourse.vue"),
  },
  {
    path: "/courselearning/create_new_course",
    name: "CreateNewCourse",
    component: () => import("../views/CourseLearning/CreateNewCourse.vue"),
  },
  {
    path: "/about",
    name: "about",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/AboutView.vue"),
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
